<template>
  <div class="temperatures">
    <v-container>
        <v-row justify-center dense>
          <v-col cols="12" lg="12" md='12' sm='12' xs='6' class="pa-0">
            <div class="d-flex justify-center flex-wrap" >
                <Temperature  v-for="(id, key) in $displayOrder($view.tempSensors)" :key="key" class="ma-1" :view="id" :cursor="'pointer'"/>
            </div>
          </v-col>
        </v-row>
    </v-container>
  <ReadingsDetails v-if="dialog" v-model="dialog" :id="activeID"/>
  </div>
</template>

<script>
  import Temperature from '@/components/Temperature.vue'
  import eventBus from "../eventBus"
  import ReadingsDetails from '@/components/ReadingsDetailDialog.vue'

  export default {
    name: 'temperatures',
    components: {
      Temperature,
      ReadingsDetails,
    },
    data: () => ({
      view:{},
      activeID:'',
      readingsObj:{},
      dialog:false,
      cursor:'pointer',
    }),
    created(){
      eventBus.$on('details',this.openDetailsDialog)
    },
    mounted (){
      window.scrollTo(0, 0)
    
    },
    methods:{
      openDetailsDialog(id){
        this.activeID = id
        this.dialog = !this.dialog
      },
    },
  }
</script>

<style scoped>
  .container {
      width: 100%;
      padding: 0px;
  }  
  .temperatures {
    overflow: hidden;
  }
</style>