<template>
    <div class="temperature">
        <v-card width="179" max-height="100" outlined :style="viewRule_Box">
            <v-container class="px-0" @click="showDialog(view._id)" :style="hover">
                <v-layout  >
                    <v-flex class="">
                        <div class="boxtitle  text-center">
                            <v-card-subtitle class="pa-0"><span class="overline">{{view.name}}</span></v-card-subtitle>
                        </div>
                        <div class="boxvalue text-center mt-n3  d-flex justify-center">
                        <div class="">
                            <v-icon v-if="online" v-show="$root.readingsTest[view._id].ticker === 'up' || !$root.readingsTest[view._id].ticker ==='none'" class="pr-1"  color="hot">{{$icon.mdiArrowTopRight}}</v-icon>
                            <v-icon v-if="online" v-show="$root.readingsTest[view._id].ticker === 'down' || !$root.readingsTest[view._id].ticker ==='none'" class="pr-1" color="cold">{{$icon.mdiArrowBottomRight}}</v-icon>
                        </div>
                        <div class="mh mb-1"> 
                            <span  :style="viewRule_Value">{{view.value}} </span>
                            <!-- <span v-if="online" v-show="view.ticker === 'up'">{{view.ticker}}</span> -->
                        </div>
                        </div>
                    </v-flex>
                </v-layout>
                <v-layout>
                    <v-flex>
                        <div class="minMaxValue text-center ">
                            <span v-if="online">{{$root.readingsTest[view._id].min.value}}째</span>
                            <span v-else>--</span>  
                        </div>
                    </v-flex>
                    <v-spacer></v-spacer>
                    <v-spacer></v-spacer>
                    <v-flex>
                        <div class="minMaxValue text-center">
                            <span v-if="online">{{$root.readingsTest[view._id].max.value}}째</span>
                            <span v-else>--</span> 
                        </div>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
    </div>
</template>

<script>
import eventBus from "../eventBus";
  export default {
    props:['view','cursor', ],
    name:'temperature',
    data: () => ({
      coldTicker:false,
      hotTicker:false,
      rule : {
        value: {
          color: '',
        },
        background: {
          color: ''
        }
      }
    }),
    created(){
      //console.log(this.id)
    },
    methods:{
      showDialog(id){
          eventBus.$emit('details',id)
      },
    },
    computed:{
      hover(){
        return {
            '--cursor': this.cursor
        }
      },
      online(){
        if (this.view.value != '--' && Object.keys(this.$root.readingsTest).length > 0){
          return true
        } else {
          return false
        }
      },
    },
  }
</script>

<style scoped>
.mh{
  max-height: 33px;
}
.boxtitle{
  width: 100%;
  margin-bottom: 5px;
  /* padding-top: 5px; */
  font-size: 15px;
  
}
.boxvalue {
  font-size: 22px;
  font-weight: 700;
}
.minMaxValue{
    font-size:16px;
    margin-top: -5px;
}
.container {
    width: 100%;
    padding-top: 2px;
    padding-bottom: 2px;
    margin-right: auto;
    margin-left: auto;
    cursor: var(--cursor)
}
</style>